import { isArray } from './isType';
export const deepClone = <T extends any = any>(obj: T): T => {
  // @ts-ignore Â∏∏ËßÅÁöÑ Èùû ÂÄº
  if ([null, undefined, NaN, false].includes(obj)) {
    return obj;
  }
  // ÂéüÂßãÊï∞ÊçÆÁ±ªÂûã
  if (typeof obj !== 'object') {
    return obj;
  }
  const root: any = isArray(obj) ? [] : {};
  // ÂÆö‰πâÂæ™ÁéØÊ†à
  const loopList: any = [
    {
      parent: root,
      key: undefined,
      data: obj,
    },
  ];

  while (loopList.length) {
    // Ê∑±Â∫¶‰ºòÂÖàÈÅçÂéÜ
    const { parent, key, data } = loopList.pop() as any;

    // ÂàùÂßãÂåñËµãÂÄºÁõÆÊ†áÔºåkey‰∏∫undefinedÂàôÊã∑Ë¥ùÂà∞Áà∂ÂÖÉÁ¥†ÔºåÂê¶ÂàôÊã∑Ë¥ùÂà∞Â≠êÂÖÉÁ¥†
    let res = parent;
    if (typeof key !== 'undefined') {
      res = parent[key] = isArray(data) ? [] : {};
    }

    for (let [childKey, value] of Object.entries(data)) {
      if (
        typeof value === 'object' &&
        value !== null &&
        ![Date, RegExp, Function].some((_type) => value instanceof _type)
      ) {
        //  ÊôÆÈÄöÂØπË±°ÂÖ•Ê†àÔºåÁïôÂæÖÂêéÁª≠Âæ™ÁéØÂ§ÑÁêÜ
        loopList.push({
          parent: res,
          key: childKey,
          data: value,
        });
      } else {
        //  ÁâπÊÆäÂØπË±°ÔºåÁõ¥Êé•ËµãÂÄº
        res[childKey] = value;
      }
    }
  }

  return root;
};
