export function formatDate(number: number, format: string = 'Y-M-D h:m:s') {
  const formateArr = ['Y', 'M', 'D', 'h', 'm', 's'];
  const returnArr = [];

  const date = new Date(number);
  returnArr.push(date.getFullYear());
  returnArr.push(formatNumber(date.getMonth() + 1));
  returnArr.push(formatNumber(date.getDate()));

  returnArr.push(formatNumber(date.getHours()));
  returnArr.push(formatNumber(date.getMinutes()));
  returnArr.push(formatNumber(date.getSeconds()));

  for (const i in returnArr) {
    format = format.replace(formateArr[i], returnArr[i] + '');
  }
  return format;
}

//Êï∞ÊçÆËΩ¨Âåñ
function formatNumber(n: number) {
  let temp = n.toString();
  return temp[1] ? temp : '0' + temp;
}
/**
 * Ëé∑Âèñ‰∏ä‰∏™ÊúàÁöÑÁ¨¨‰∏ÄÂ§©
 */
export const lastMonthDay = (): Date => {
  const d = new Date();
  const year = d.getFullYear();
  const month = d.getMonth();
  if (month === 0) {
    d.setFullYear(year - 1);
    d.setMonth(11);
  } else {
    d.setMonth(month - 1);
  }
  d.setDate(1);
  d.setHours(0, 0, 0, 0);
  return d;
};

/**
 * Ëé∑ÂèñËøô‰∏™ÊúàÁöÑÁ¨¨‰∏ÄÂ§©
 */
export const thisMonthDay = (): Date => {
  const d = new Date();
  d.setDate(1);
  d.setHours(0, 0, 0, 0);
  return d;
};

/**
 * Êò®Â§© 0:0:0 Âà∞‰ªäÂ§© 23:59:59
 */
export const recentDay = (): [Date, Date] => {
  const start = new Date();
  start.setDate(start.getDate() - 1);
  start.setHours(0, 0, 0, 0);
  const end = new Date(start.getTime() + 1000 * 60 * 60 * 24);
  end.setHours(23, 59, 59);
  return [start, end];
};

/**
 * ‰∏ä‰∏™Êúà 1Âè∑ Âà∞‰ªäÂ§© 23:59:59
 */
export const recentMonth = (): [Date, Date] => [lastMonthDay(), recentDay()[1]];

/**
 * Ëøô‰∏™Êúà 1Âè∑ Âà∞‰ªäÂ§© 23:59:59
 */
export const thisMonth = (): [Date, Date] => [thisMonthDay(), recentDay()[1]];

/**
 * yyyymmdd -> yyyy-mm-dd
 */
export const ymdParse = (daytte: number | string, sep = '-') => {
  let str = String(daytte).split('');
  str.splice(4, 0, sep);
  str.splice(7, 0, sep);
  return str.join('');
};
/**
 * Ëé∑ÂèñÊòéÂ§©
 */
export const nextDay = (h = 0, m = 0, s = 0) => {
  const d = new Date();
  d.setDate(d.getDate() + 1);
  d.setHours(h, m, s);
  return d;
};

