import { isString, isObject } from './isType';
/**
 * È™åËØÅÊòØÂê¶‰∏∫ÂêàÊ≥ïÁöÑ email
 * @param {string} email
 */
export const isEmail = (email: string) =>
  /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z0-9]+$/.test(email);
/**
 * È™åËØÅÊòØÂê¶‰∏∫ÂêàÊ≥ïÁöÑÊâãÊú∫Âè∑
 * @param {string} tel
 */
export const isMobile = (tel: string) => /^1[23456789]\d{9}$/.test(tel);
/**
 * È™åËØÅÊòØÂê¶‰∏∫ÂêàÊ≥ïÁöÑ http url
 * @param {string} url
 */
export const isHttpUrl = (url: string) =>
  /http(s)?:\/\/([\w-]+\.)+[\w-]+(\/[\w-.\/?%&=]*)?/.test(url);
/**
 * È™åËØÅÊòØÂê¶‰∏∫ÂêàÊ≥ïÁöÑ websocket url
 * @param {string} url
 */
export const isWebSocketUrl = (url: string) =>
  /ws(s)?:\/\/([\w-]+\.)+[\w-]+(\/[\w-.\/?%&=]*)?/.test(url);
/**
 * È™åËØÅÊòØÂê¶‰∏∫ÂêàÊ≥ïÁöÑÊó•ÊúüÊ†ºÂºè
 * @param {string} date
 */
export const isDate = (date: string | number | Date) =>
  !/Invalid|NaN/.test(new Date(date).toString());
/**
 * È™åËØÅÊòØÂê¶‰∏∫ÂêàÊ≥ïÁöÑISOÊó•ÊúüÊ†ºÂºè
 * @param {string} date
 */
export const isISODate = (date: string) =>
  /^\d{4}[\/\-](0?[1-9]|1[012])[\/\-](0?[1-9]|[12][0-9]|3[01])$/.test(new Date(date).toString());
/**
 * ÊòØÂê¶‰∏∫ÂçÅËøõÂà∂Êï∞
 * @param {number} num
 */
export const isDecimal = (num: string | number) =>
  /^(?:-?\d+|-?\d{1,3}(?:,\d{3})+)?(?:\.\d+)?$/.test(num + '');
/**
 * ÊòØÂê¶‰∏∫Êï¥Êï∞
 * @param {number} num
 */
export const isInteger = (num: string | number) => /^\d+$/.test(num + '');
/**
 * ÊòØÂê¶‰∏∫ÂêàÊ≥ïÁöÑË∫´‰ªΩËØÅ
 * @param {number} id
 */
export const isIdCard = (id: string | number) =>
  /^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/.test(id + '');
/**
 * ÊòØÂê¶‰∏∫ÂêàÊ≥ïËΩ¶ÁâåÂè∑
 * @param {string} value
 */
export const isLicensePlate = (value: string) => {
  // Êñ∞ËÉΩÊ∫êËΩ¶Áâå
  const xreg =
    /^[‰∫¨Ê¥•Ê≤™Ê∏ùÂÜÄË±´‰∫ëËæΩÈªëÊπòÁöñÈ≤ÅÊñ∞ËãèÊµôËµ£ÈÑÇÊ°ÇÁîòÊôãËíôÈôïÂêâÈóΩË¥µÁ≤§ÈùíËóèÂ∑ùÂÆÅÁêº‰ΩøÈ¢ÜA-Z]{1}[A-Z]{1}(([0-9]{5}[DF]$)|([DF][A-HJ-NP-Z0-9][0-9]{4}$))/;
  // ÊóßËΩ¶Áâå
  const creg =
    /^[‰∫¨Ê¥•Ê≤™Ê∏ùÂÜÄË±´‰∫ëËæΩÈªëÊπòÁöñÈ≤ÅÊñ∞ËãèÊµôËµ£ÈÑÇÊ°ÇÁîòÊôãËíôÈôïÂêâÈóΩË¥µÁ≤§ÈùíËóèÂ∑ùÂÆÅÁêº‰ΩøÈ¢ÜA-Z]{1}[A-Z]{1}[A-HJ-NP-Z0-9]{4}[A-HJ-NP-Z0-9ÊåÇÂ≠¶Ë≠¶Ê∏ØÊæ≥]{1}$/;
  if (value.length === 7) {
    return creg.test(value);
  } else if (value.length === 8) {
    return xreg.test(value);
  } else {
    return false;
  }
};
/**
 * ÊòØÂê¶‰∏∫‰∏≠ÊñáÔºà‰∏çÂåÖÂê´Ê†áÁÇπÁ¨¶Âè∑Ôºâ
 * @param {string} str
 */
export const isChinese = (str: string) => /^[\u4e00-\u9fa5]+$/gi.test(str);
/**
 * ÊòØÂê¶‰∏∫Ëã±ÊñáÂ≠óÊØç
 * @param {string} str
 */
export const isLetter = (str: string) => /^[a-zA-Z]+$/.test(str);
/**
 * ÊòØÂê¶‰∏∫ÂêàÊ≥ïÁöÑÂ∫ßÊú∫Âè∑Á†Å
 * @param {string} value
 */
export const isLandline = (value: string) => /^\d{3,4}-\d{7,8}(-\d{3,4})?$/.test(value);
/**
 * ÊòØÂê¶‰∏∫ JSON Â≠óÁ¨¶‰∏≤
 * @param {string} str
 */
export const isJsonStr = (str: any) => {
  if (isString(str)) {
    try {
      const obj = JSON.parse(str);
      if (isObject(obj) || Array.isArray(obj)) {
        return true;
      } else {
        return false;
      }
    } catch (error) {
      return false;
    }
  }
  return false;
};
/**
 * Âà§Êñ≠ÊòØÂê¶‰∏∫ÂêàÊ≥ïÁöÑË¥¢Âä°Êï∞Â≠ó(ÂçÉÂàÜÁ¨¶Â≠óÁ¨¶‰∏≤)
 * @param {string} str
 */
export const isCurrencyStr = (str: string) => /^-?\d{1,3}(,\d{3})*(\.\d+)?$/.test(str);
/**
 * Âà§Êñ≠ÊòØÂê¶‰∏∫ÂæÆ‰ø°ÂÜÖÁΩÆÊµèËßàÂô®
 */
export const isWeChat = () => /MicroMessenger/gim.test(navigator.userAgent);
/**
 * Âà§Êñ≠ÊòØÂê¶‰∏∫ iOS ÊµèËßàÂô®
 */
export const isiOS = () => !!navigator.userAgent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/i);
/**
 * Âà§Êñ≠ÊòØÂê¶‰∏∫ macOS ÊµèËßàÂô®
 */
export const isMacOS = () => /Mac OS X/i.test(navigator.userAgent);
/**
 * Âà§Êñ≠ÊòØÂê¶‰∏∫ÂÆâÂçìÊµèËßàÂô®
 */
export const isAndroid = () => {
  const u = navigator.userAgent;
  return u.indexOf('Android') > -1 || u.indexOf('Adr') > -1;
};
